.hireWrapper {
  height: 20vh;
  background-color: darkkhaki;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hireContent {
  border: 2px solid;
  height: 15vh;
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.hireTitle {
  background-color: grey;
  font-size: 16px;
}

.hireButton {
  background-color: dimgrey;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}